html { font-family: "Helvetica Neue",Helvetica,STHeiTi,Arial,sans-serif; -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; font-size: 62.5%; }

body { margin: 0; font-size: 0.6rem; line-height: 1.5; color: #333; background: #fff; min-height: 100%; overflow-x: hidden; -webkit-overflow-scrolling: touch; }

article, aside, details, figcaption, figure, footer, header, hgroup, main, nav, section, summary { display: block; }

audio, canvas, progress, video { display: inline-block; vertical-align: baseline; }

audio:not([controls]) { display: none; height: 0; }

[hidden], template { display: none; }

svg:not(:root) { overflow: hidden; }

a { background: transparent; text-decoration: none; -webkit-tap-highlight-color: transparent; color: #08c; }

a:active { outline: 0; }

a:active { color: #069; }

abbr[title] { border-bottom: 1px dotted; }

b, strong { font-weight: bold; }

dfn { font-style: italic; }

mark { background: #ff0; color: #000; }

small { font-size: 80%; }

sub, sup { font-size: 75%; line-height: 0; position: relative; vertical-align: baseline; }

sup { top: -0.5em; }

sub { bottom: -0.25em; }

img { border: 0; vertical-align: top; }

hr { -moz-box-sizing: content-box; box-sizing: content-box; height: 0; }

pre { overflow: auto; white-space: pre; white-space: pre-wrap; word-wrap: break-word; }

code, kbd, pre, samp { font-family: monospace,monospace; font-size: 1em; }

button, input, optgroup, select, textarea { color: inherit; font: inherit; margin: 0; }

button { overflow: visible; }

button, select { text-transform: none; }

button, html input[type="button"], input[type="reset"], input[type="submit"] { -webkit-appearance: none; cursor: pointer; }

button[disabled], html input[disabled] { cursor: default; }

button::-moz-focus-inner, input::-moz-focus-inner { border: 0; padding: 0; }

input { line-height: normal; outline: 0; }

input[type="checkbox"], input[type="radio"] { padding: 0; }

input[type="number"]::-webkit-inner-spin-button, input[type="number"]::-webkit-outer-spin-button { height: auto; }

input[type="search"] { -webkit-appearance: textfield; }

input[type="search"]::-webkit-search-cancel-button, input[type="search"]::-webkit-search-decoration, input[type="text"] { -webkit-appearance: none; }

fieldset { border: 1px solid #c0c0c0; margin: 0 2px; padding: .35em .625em .75em; }

legend { border: 0; padding: 0; }

textarea { overflow: auto; resize: vertical; }

optgroup { font-weight: bold; }

table { border-collapse: collapse; border-spacing: 0; }

td, th { padding: 0; }

html, button, input, select, textarea { font-family: "Helvetica Neue",Helvetica,STHeiTi,Arial,sans-serif; }

h1, h2, h3, h4, h5, h6, p, figure, form, blockquote { margin: 0; }

ul, ol, li, dl, dd { margin: 0; padding: 0; }

ul, ol { list-style: none outside none; }

input::-moz-placeholder, textarea::-moz-placeholder { color: #ccc; }

input:-ms-input-placeholder, textarea:-ms-input-placeholder { color: #ccc; }

input::-webkit-input-placeholder, textarea::-webkit-input-placeholder { color: #ccc; }

* { -webkit-box-sizing: border-box; -moz-box-sizing: border-box; box-sizing: border-box; }

.fl { float: left; }

.fr { float: right; }

.clearfix { *zoom: 1; }

.clearfix:before, .clearfix:after { content: ""; display: table; }

.clearfix:after { clear: both; overflow: hidden; }

@media screen and (min-width: 212px) { html { font-size: 13.3125px; } }

@media screen and (min-width: 319px) { html { font-size: 20px; } }

@media screen and (min-width: 359px) { html { font-size: 22.5px; } }

@media screen and (min-width: 383px) { html { font-size: 24px; } }

@media screen and (min-width: 399px) { html { font-size: 25px; } }

@media screen and (min-width: 414px) { html { font-size: 25.9375px; } }

@media screen and (min-width: 423px) { html { font-size: 26.5px; } }

@media screen and (min-width: 479px) { html { font-size: 30px; } }

@media screen and (min-width: 539px) { html { font-size: 33.75px; } }

@media screen and (min-width: 639px) { html { font-size: 40px; } }

@media screen and (min-width: 640px) { html { font-size: 40.0625px; } }

@media only screen and (min-device-width: 320px) and (max-device-width: 568px) { html { font-size: 20px; } }

@media (device-width: 375px) and (-webkit-min-device-pixel-ratio: 2) { html { font-size: 23.4375px; } }

@media (device-width: 414px) and (-webkit-min-device-pixel-ratio: 3) { html { font-size: 25.875px; } }

.test { width: 2.5rem; height: 2.5rem; transform: rotate(40deg); background: blue; }
